import {mapGetters} from 'vuex';
import BScroll from 'better-scroll'
export default {
  name: "test",
  computed: {
    getName(){
      return this.$store.state.name
    },
    ...mapGetters(['forList'])
  },
  mounted (){
    console.log(this.$store.state.test,'*sssssssssssssss')
  },
  methods:{
    changeName () {      // 点击修改全局$store.state.name 的值
      this.$store.commit('updateName',this.alterName)
    },
    addCount(){
      this.$store.commit('updateCount')
    },
    seveId() {    //用缓存保存传过来的id值
      let seid = this.$route.params.dataObj
      if (seid == undefined){
        this.huancun = sessionStorage.getItem("id")
        return
      }else {
        sessionStorage.setItem('id',this.$route.params.dataObj)
        this.huancun = sessionStorage.getItem("id")
      }
    }
  },
  data(){
    return{
      alterName:'',
      sildename:'slide-go',
      canshu:'',
      huancun:'',
      huancun2:''
    }
  },
}



